idth: 60%;\n      }\n      .me-chatcontent{\n        p{\n          background-color: $greenColor;\n        }\n      }\n      .get-chatcontent{\n        p{\n          background-color: $whiteColor;\n          .click-lingqu{\n            color: $blueColor;\n          }\n        }\n      }\n      .me-chatcontent,.get-chatcontent{\n          max-width: 70%;\n          p{\n            width: 100%;\n            min-height: 75px;\n            text-align:left;\n            text-align: justify;\n            white-space: normal;\n            word-wrap: break-word;\n            word-break: break-all;\n            box-sizing: border-box;\n            padding: 17px;\n            border-radius: 10px;\n          }\n      }\n    }\n    .my-info{\n      flex-direction: row-reverse;\n    }\n    .get-info{\n      flex-direction: row;\n    }\n  }\n  .footer{\n    width: 100%;\n    height: 100px;\n    position: absolute;\n    bottom: 0;\n    background-color: $LightGreyColor;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    input{\n      width: 60%;\n      height: 70px;\n      margin-right: 15px;\n      background-color: $whiteColor;\n      border-radius: 10px;\n      border: none;\n      box-sizing: border-box;\n      padding: 0px 10px;\n    }\n    button{\n      width: 15%;\n      height: 65px;\n      border: none;\n      background-color: $greenColor;\n      color: $whiteColor;\n      border-radius: 10px;\n    }\n  }\n}\n/* 领取 */\n.receive{\n  width: 100%;\n    height: 100%;\n  overflow: hidden;\n  position: relative;\n  .longbi{\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    z-index: -9999;\n  }\n  .height{\n    height: 75%;\n  }\n  .lingqu-but{\n    width: 30%;\n    display: block;\n    margin: auto;\n  }\n  .p{\n    width: 90%;\n    color: $whiteColor;\n    text-align: center;\n    margin: 20px auto;\n  }\n  .guanbi{\n    display: block;\n      text-align: center;\n      font-size: 60px;\n      color: $whiteColor;\n  }\n}\n/* 领取成功 */\n.success{\n  height: 100%;\n  overflow: hidden;\n  .icon-style{\n    display: block;\n    margin: 50% auto auto auto;\n    font-size: 150px;\n    text-align: center;\n    color: $blueColor;\n  }\n  .success-text{\n    text-align: center;\n    margin: 30px auto;\n    font-size: 35px;\n  }\n  button{\n    width: 60%;\n    height: 80px;\n    display: block;\n    margin:150px auto;\n    background-color: $blueColor;\n    border: none;\n    color: $whiteColor;\n    font-size: 35px;\n    border-radius: 100px;\n  }\n}\n/* 领取失败 */\n.receiveerr{\n  position: relative;\n  height: 100%;\n  overflow: hidden;\n  .err{\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba($color: #000000, $alpha: 0.7);\n    img{\n      width: 100%;\n    }\n    .bott{\n      width: 50%;\n      height: 80px;\n      background-color: $jinseColor;\n      border-radius: 30px;\n      margin: 40px auto;\n      overflow: hidden;\n      button{\n        width: 95%;\n        height: 65px;\n        display: block;\n        border-radius: 20px;\n        margin: 4px auto auto auto;\n        border: none;\n        background-image: linear-gradient(to top, $anhongColor, $danhongColor);\n        color: $jinseColor;\n        font-size: 38px;\n        font-weight: 700;\n        letter-spacing:3px;\n      }\n    }\n    .guanbi{\n      display: block;\n      width: 85%;\n      text-align: right;\n      margin:20% auto 30px auto;\n      font-size: 60px;\n      color: $whiteColor;\n    }\n    p{\n      text-align: center;\n      color: $whiteColor;\n      line-height: 50px;\n    }\n  }\n}","$mainColor: #f0652e;\n$orangeColor:#ff9900; // 橙色\n$blueColor:#03c; // 蓝色\n$textBlueColor: #09c; // 文字蓝\n$skyBlueColor:#19D5FD; // 天蓝\n$haiColor:#18b5cd; // 海蓝\n$greyColor: #999; //字体灰色\n$bgGreyColor:#aeaeae; // 背景灰\n$LightGreyColor: #f3f3f3; // 浅灰色\n$whiteColor:#fff; // 白色\n$redColor:red; // 红色\n$danhongColor:#f36246; // 淡红\n$anhongColor:#c2301c; // 暗红\n$pinkColor